// greeting.js

import GreetingView from './greeting-view';
import showScreen from '../show-screen';
import App from '../main';


export default class Greeting {
  constructor() {
    this.view = new GreetingView();
  }

  init() {
    showScreen(this.view);

    this.view.onStep = () => {
      App.showRules();
    };

  }
}
