// stats.js

import GameStatsView from './stats-view';
import showScreen from '../show-screen';
import App from '../main';

export default class Stats {
  constructor() {
    this.view = new GameStatsView();
  }
  init() {
    showScreen(this.view);
    this.view.onBack = () => {
      App.showRules();
    };
  }
}
