import {DefaultAdapter} from '../model';

export default new class extends DefaultAdapter {
  preprocess(data) {
    const preprocessed = {};
    Object.keys(data).forEach((it, i) => {
      preprocessed[`level=${i}`] = {
        text: data[it].text,
        answers: data[it].answers
      };
    });
  }
  toServer(data) {
    return JSON.stringify(data);
  }
}();
