// intro.js

import IntroView from './intro-view';
import showScreen from '../show-screen';
import App from '../main';

export default class Intro {
  constructor() {
    this.view = new IntroView();
  }
  init() {
    App.showIntro();
    showScreen(this.view);

    this.view.onStep = () => {
      App.showGreeting();
    };
  }
}

